import { Component, OnInit } from "@angular/core";
import { DataService } from '../services/data.service';
import { UserAnswer, Answer } from '../test-page/question';

@Component({
    selector: 'app-admin-page',
    templateUrl: 'admin-page.component.html',
    styleUrls: ['admin-page.component.scss']
})

export class AdminPageComponent implements OnInit { 

    public results: UserAnswer[] = [];

    constructor(private dataService: DataService) { }

    ngOnInit() {
        this.dataService.getResults().then(data => {
            this.results = data;
        })
    }

    public getPercents(answers: Answer[]): number {
        const total = answers.reduce((accum, item) => accum += item.cost, 0);
        return Math.round(total * 100 / 81);
    }

    public isTrue(answer: Answer): boolean {
        return answer.cost && answer.cost > 0;
    }
}
